.coursemap-title {
  color: #838383; }

.card.coursemap-listing {
  position: relative;
  border-radius: 10px;
  padding: 4rem 0;
  border-color: #77b02a;
  background: #fafafa; }

.card.coursemap-listing--disabled {
  border-color: #eee; }
  .card.coursemap-listing--disabled .coursemap-listing__title {
    color: #8e8e8e; }
  .card.coursemap-listing--disabled .coursemap-listing__subtext {
    color: #9e9e9e; }

.card.coursemap-listing--active {
  background: #77b02a; }
  .card.coursemap-listing--active .coursemap-listing__title {
    color: white; }
  .card.coursemap-listing--active .coursemap-listing__subtext {
    color: #e3f3cd; }
  .card.coursemap-listing--active .coursemap-listing__number {
    border: 1px solid white;
    color: white; }
  .card.coursemap-listing--active .coursemap-listing__btn {
    border-color: #3f5e16;
    background: #3f5e16;
    padding: 0.5rem 2rem; }

.coursemap-listing__title,
.coursemap-listing__subtext {
  color: #77b02a; }

.coursemap-listing__icon {
  position: absolute;
  top: 20px;
  right: 20px; }

.coursemap-listing__number {
  width: 50px;
  height: 50px;
  border: 1px solid #77b02a;
  color: #77b02a;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center; }

.nav-link-bg {
  background: #30363c; }
